@media screen and (max-width: 1000px) {
  .quiz__container {
    display: flex;
    width: 100%;
    flex-direction: column;
    color: #f8f7f4;
    text-align: center;
    color: #f8f7f4;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-weight: 500;
    background-color: #0a0a0a;
    margin: 20px 0px;
    padding: 50px 0px;
  }
  .quiz__container button {
    background-color: #0067f4;
    border-radius: 24px;
    width: 115px;
    height: 3.9vh;
    color: #f8f7f4;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI",
      Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue",
      sans-serif;
    font-weight: 500;
    margin: 0px 10vw;
    border: 0;
    font-size: 13px;
    cursor: pointer;
  }
  .quiz__container h2 {
    font-size: 0.8rem;
    font-weight: 500;
    user-select: none;
  }
  .quiz__container h1 {
    font-size: 1.2rem;
    user-select: none;
  }
}
